import React, { useEffect, useState } from "react";

import { Collapse } from "antd-mobile";
import { NavLink } from "react-router-dom";
import { routes } from "../../router";

const __collapse = "__collapse"

const Home = ({ location }) => {
	const [isCollapse, setIsCollapse] = useState('')

	useEffect(() => {
		const coll = localStorage.getItem(__collapse)
		setIsCollapse(coll)

	}, [])

	useEffect(() => {
		return () => {
			localStorage.setItem(__collapse, isCollapse)
		}
	}, [isCollapse])


	const onChange = (value) => {
		setIsCollapse(value)
		localStorage.setItem(__collapse, value)
	}



	return (
		<div>
			<Collapse accordion={true} onChange={onChange} activeKey={isCollapse}>
				{routes.map((route) => {
					if (route.hidden) return null;
					return route.children ? (
						<Collapse.Panel
							key={`${route.id}`}
							title={<span className="ellipsis">{route.name}</span>}
						>
							{route.children.map((c) => (
								<div key={c.id}>
									<NavLink
										className="ellipsis"
										to={c.url ? c.url + '/' + c.id : c.path}
										activeStyle={{ color: "#f60" }}
										style={{ textDecoration: "none", color: "inherit" }}
									>
										{c.name}
									</NavLink>
								</div>
							))}
						</Collapse.Panel>
					) : (
						<Collapse.Panel
							key={`${route.id}`}
							arrow={false}
							title={
								<NavLink
									className="ellipsis"
									to={route.url ? route.url + '/' + route.id : route.path}
									activeStyle={{ color: "#f60" }}
									style={{ textDecoration: "none", color: "inherit" }}
								>
									{route.name}
								</NavLink>
							}
						></Collapse.Panel>
					);
				})}
			</Collapse>
		</div>
	);
};

export default Home;
